<template>
  <v-toolbar dark fixed color="primary">
    <v-btn icon style="margin-right:18px;">
      <v-icon>apps</v-icon>
    </v-btn>

    <router-link
      :to="item.path"
      v-ripple
      class="nav-link"
      v-for="(item,index) in menus"
      :key="index"
    >{{$t("header."+item.name)}}</router-link>
    <v-spacer></v-spacer>

    <v-btn icon @click="login">
      <v-icon>search</v-icon>
    </v-btn>

    <v-btn icon @click="handleSignOut">
      <v-icon>chat</v-icon>
    </v-btn>
    <v-btn icon @click="handleCutover">{{ btntext }}</v-btn>
  </v-toolbar>
</template>
<script>
export default {
  data() {
    return {
      btntext:"中文"
    };
  },
  created() {
    const { options } = this.$router;
    this.menus = options.routes[0].children.map(item => {
      item["active"] = false;
      return item;
    });
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    locale(key) {
      return this.$t("header." + key);
    }
  },
  mounted() {
    // console.log(this.$t("header.moving"))
  },
  methods: {
    login() {
      this.$store.commit("handleSignIn");
    },
    handleSignOut() {
      this.$store.commit("handleSignOut");
    },
    handleCutover() {
      if (this.$i18n.locale === "zh_CN") {
        this.$i18n.locale = "en_US";
        this.btntext = "中文"
      } else {
        this.$i18n.locale = "zh_CN";
        this.btntext = "EN"
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.nav-link {
  font-size: 16px;
  font-weight: 700;
  display: block;
  color: #ffffff;
  line-height: 64px;
  padding: 0 24px;
  text-decoration: none;
}
.nav-link.router-link-active {
  background: rgba($color: #ffffff, $alpha: 0.2);
}
</style>
